@import url('https://rsms.me/inter/inter.css');
html {
  font-family: 'Inter', sans-serif;
}
@supports (font-variation-settings: normal) {
  html {
    font-family: 'Inter var', sans-serif;
  }
}

* {
  margin: 0px;
  padding: 0;
  box-sizing: border-box;
  font-size: 16px;
}

body {
  background-color: black;
  color: white;
  h1 {
    font-size: 3rem;
    text-align: center;
    vertical-align: middle;
  }
  a {
    color: white;
  }
  section {
    color: black;
    .myBarChart {
      background-color: white;
    }
    svg {
      &:nth-child(1) {
        display: none;
      }
      display: block;
      margin: auto;
      margin: 50px auto 50px auto;
      background-color: white;
      position: relative;
      rect {
        fill: royalblue;
        opacity: 0.8;
      }
      rect:hover {
        opacity: 1;
      }
      circle {
        fill: red;
        opacity: 0.5;
      }
      circle:hover {
        opacity: 1;
      }
      text {
        font-size: 12px;
        color: grey;
      }
      .graphTitle {
        font-size: 1.75rem;
        font-weight: 700;
      }
      .axisTitle {
        font-size: 16px;
        fill: black;
        text-anchor: middle;
      }
      .extraText {
        font-size: 16px;
        text-align: right;
      }
      .lolliLine {
        opacity: 0.8;
        stroke: grey;
        stroke-width: 2px;
      }
      .lolliCircle {
        fill: steelblue;
        opacity: 0.8;
        stroke: black;
      }
    }

